/* SCROLL BAR */

/* width */
::-webkit-scrollbar {
  width: 12px;
  background-color: #f5f5f5;
}

/* Track */
::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  background-color: #f5f5f5;
}

/* Handle */
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #555;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.grid-container {
  background: rgba(255, 255, 255, 0.069);
  padding: 30px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.358);
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  grid-gap: 0px 50px;
  gap: 0px 50px;
  border-radius: 5px;
  height: 1000px;
  width: 1530px;
  background-color: #ffffff;
  display: grid;
  margin-top: 50px;
  margin-left: 50px;
  margin-bottom: 50px;
  grid-template-areas:
    ". . ."
    ". . ."
    ". . .";
}
